import React from 'react';
import jwt from 'jsonwebtoken';
import Iframe from 'react-iframe';

import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow} from '@coreui/react';

const METABASE_SITE_URL = "http://metabase.sccic.cloudns.asia";
const METABASE_SECRET_KEY = "72f61ae1459e6e6823e7f7153f82fcdccb463018cdb6f07eacb98eb3c1e15a70";

const payload = {
    resource: { dashboard: 1 },
    params: {},
    exp: Math.round(Date.now() / 1000) + (10 * 60) // 10 minute expiration
};

export default class Dashboard1 extends React.Component {

    constructor(props) {
        super(props);
        this.state = {iframeUrl: null};
    }

    componentDidMount() {
        const token = jwt.sign(payload, METABASE_SECRET_KEY);
        console.log(token);
        const iframeUrl = METABASE_SITE_URL + "/embed/dashboard/" + token + "#bordered=false&titled=true";
        console.log(iframeUrl);
        this.setState({iframeUrl: iframeUrl});
    }

    render() {
        return (
            <CRow>
                <CCol>
                    <CCard>
                        <CCardHeader>
                            <h5>Demo Dashboard 1</h5>
                        </CCardHeader>
                        <CCardBody>
                            <Iframe
                                url={this.state.iframeUrl}
                                width="100%"
                                height="370px"
                                overflow= "hidden"
                                scrolling= "no"
                                frameBorder= "0"
                            />
                        </CCardBody>
                    </CCard>
                </CCol>
            </CRow>
        );
    }
}
